.Quiz {
    overflow: scroll;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #58bf9e;
    box-sizing: border-box;
    color: white;
}
.Button {
    margin-top: 48px;
}
button:focus {
    outline: none;
}
.Button>button{
    margin: 18px; 
    cursor: pointer;  
    padding: 12px 18px;
    border-radius: 24px;
    background-color: white;
    color: #58bf9e;
    font-weight: bold;
    font-size: 18px;
    border: none;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.Button>button.active {
    border: 3px solid #127152;
    color: #127152;
}

.Button>button:hover {
    color: #127152;
}
.Instructions {
    font-weight: bold;
    margin-top: 24px;
}

.QuizInterface .Question {
    font-size: 64px;
    font-weight: bold;
}

.QuizInterface .answerInput {
    background-color: #58bf9e;
    border: none;
    border-bottom: 5px solid white;
    margin: 12px;
    color: white;
    font-weight: bold;
    font-size: 36px;
    width: 120px;
    text-align: center;
}

.QuizInterface .answerInput:focus {
    outline: none;
}

.QuizInterface .submitButton,
.closeButton {
    margin: 18px; 
    cursor: pointer;  
    padding: 12px 18px;
    background-color: white;
    color: #58bf9e;
    border: none;
    font-weight: bold;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.Result{
    font-size: 24px;
    font-weight: bold;
}